.video-embed
{
    &__iframe-container
    {
        position: relative;
        overflow: hidden;
        width: 100%;
        padding-top: 56.25%;
        margin-bottom:10px;             
    }
    &__iframe
    {
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        width: 100%;
        height: 100%;
        border:0;
        margin:0;
        padding:0;              
    }
    
}