.responsive-iframe
{
    &__container
    {
        position: relative;
        overflow: hidden;
        width: 100%;
        padding-top: 56.25%; /* 16:9 Aspect Ratio (divide 9 by 16 = 0.5625) */      
        margin-bottom: 40px;
        @include media-up(md)
        {
            margin-bottom: 80px;
        }
    }
    &__iframe
    {
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        width: 100%;
        height: 100%;
        border: 0;
        margin:0;
    }
}