body {
	font-family: $font-primary;
	font-weight: $font-light;
	font-size: $font-size;
	line-height: $line-height;
	letter-spacing: $letter-spacing;
	background: $background;
	color: $text;
}

::selection {
	background: $primary;
	color: $foreground-negative;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	font-size: inherit;
	font-weight: inherit;
	line-height: inherit;
	margin: 0;
	padding: 0;
}

.primary {
	color: $foreground-primary !important;
}

